<template>
  <div>
    <v-row>
      <v-col class="text-center">
        <img alt="Vue big-image" src="../assets/flower.jpg" class="big-image" />
        <br />
        <br />
        <br />
        <p class="text-h3">{{ this.$route.params.flowerId }} </p>
        <p>   </p>
        <p> (Reload)   ?</p>
        <p> vue-router   </p>
        <v-pagination v-model="flowerId" :length="8" circle></v-pagination>
      </v-col>
    </v-row>

    <v-divider class="ma-8"> </v-divider>

    <v-row>
      <v-col>
        <router-link to="/">
          <v-btn block class="white" rounded> </v-btn>
        </router-link>
      </v-col>
      <v-col>
        <router-link to="/flower/1">
          <v-btn block class="white" rounded>  </v-btn>
        </router-link>
      </v-col>
      <v-col>
        <router-link to="/grass">
          <v-btn block class="white" rounded>  </v-btn>
        </router-link>
      </v-col>
      <v-col>
        <router-link to="/tree">
          <v-btn block class="white" rounded
            >  </v-btn
          >
        </router-link>
      </v-col>
    </v-row>
  </div>
</template>

<script>
export default {
  data() {
    return {
      checkbox: true, //
      sheet: false, //
      flowerId: parseInt(this.$route.params.flowerId), //
    };
  },
  computed: {},
  methods: {},
  watch: {
    flowerId(newFlowerId) {
      this.$router.push({ path: "", params: { flowerId: newFlowerId } });
    },
  },
  mounted() {},
};
</script>

<style lang="scss" scoped>
.big-image {
  border-radius: 12px;
  width: 100%;
}
